.login {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    text-align: center;
    background-color: var(--deco-green);
    color: var(--deco-grey);
}

.image-container {
    display: flex;
    justify-content: space-around;
    width: 100%;
}

.image-container img {
     width: 40%;
 }

.login h1 {
    margin-top: 5%;
    font-size: 2rem;
}

.login h2 {
    margin-bottom: 3%;
    font-size: 1.5rem;
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 80px;
    background-color: var(--deco-black);
    color: var(--deco-gold);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    box-sizing: border-box;
}

.error-banner {
    margin-bottom: 5px;
    width: 100%;
    background-color: var(--deco-red);
    color: white;
}


